export const tiemstamp = (time) => {
  var hours = Math.floor(time / (60 * 60));
  var divisor_for_minutes = time % (60 * 60);
  var minutes = Math.floor(divisor_for_minutes / 60);
  var divisor_for_seconds = divisor_for_minutes % 60;
  var seconds = Math.ceil(divisor_for_seconds);

  if (hours !== 0) {
    return `${hours} ${minutes} ${seconds}`;
  }
  return `${minutes} ${seconds}`;
};

// https://kdinner.tistory.com/68
export const timeForToday = (value) => {
  const today = new Date();
  const timeValue = new Date(value);

  const betweenTime = Math.floor(
    (today.getTime() - timeValue.getTime()) / 1000 / 60,
  );
  if (betweenTime < 1) return '';
  if (betweenTime < 60) {
    return `${betweenTime}`;
  }

  const betweenTimeHour = Math.floor(betweenTime / 60);
  if (betweenTimeHour < 24) {
    return `${betweenTimeHour}`;
  }

  const betweenTimeDay = Math.floor(betweenTime / 60 / 24);
  if (betweenTimeDay < 365) {
    return `${betweenTimeDay}`;
  }

  return `${Math.floor(betweenTimeDay / 365)}`;
};
